import React,  { useState, useEffect}  from 'react';
import { Container, Button, Form, Row, Col, Image } from 'react-bootstrap';
import useUNST from '../contracts/useUNST';
import { SettingStore } from '../stores';
import { Engine } from '../comm';

import { useUtils, usePostOffice, useImage } from '../customHooks';
import { useParams } from 'react-router';

export default (props)=> {
  const eng = new Engine();

  const { poAddress } = useParams();

  const { getUNSTInfo, createPostoffice, askPrice} = useUNST('UNST', 'https://rinkeby.infura.io/v3/3af2ebd23289490dbe29d3034e2887ce');
  const [ unstInfo, setUnstInfo ] = useState({});
  const [ formrec, setFormrec ] = useState({});
  const [ price, setPrice ] = useState(null);
  const metaImage = useImage();

  const [ po, setPo ] = useState([]);

  const { getPostOffices } = usePostOffice();
  const { copyableShow, hashLink } = useUtils();

  const handleTextChange = async (e, id)=> {
    const rec = formrec;
    rec[id] = e.target.value;
    setFormrec(rec);
  //  setTm(new Date().getTime());
  }

  useEffect(()=>{
    setPo(SettingStore.getState().po);
    askPrice(SettingStore.getState().unst, v=>setPrice(v))
  }, []);


  const btnCreatePostoffice = ()=> <Button className="btn btn-info" onClick={()=>{
      createPostoffice(SettingStore.getState().unst, formrec.name, v=>{
        SettingStore.dispatch({type:'addPo', data:v});
        eng.loadingOff();
      })
    }}>Submit funding</Button>

  const formForm = <Container fluid={true} className="p-3 bg-light rounded">
    <Container fluid={true} className="p-3 ">
      <Row>
        <Col xl={3} lg={3} md={4} sm={5} xs={6} className="p-0 pl-2 pr-2 text-left">
            <Container className="d-flex align-items-center justify-content-center p-2 rounded" 
              style={{height:'100%', width:'100%', backgroundColor:'#87CEEB'}}>
              {metaImage.showOrigin('postOffice', {className:'', style:{width:'8rem'}}) }
            </Container>
        </Col>

        <Col xl={9} lg={9} md={8} sm={7} xs={6} className="alert-secondary rounded p-3">
          <Form.Group className="m-0 p-0">
            <Form.Label className="p-0 m-0 text-info">Funding a post offic. minimum funding amount : {price} Ether</Form.Label>
          </Form.Group>

          <Form.Control type="text" defaultValue={formrec.name}
            placeholder="Enter a office name (Limit 32 characters)" className="p-1 m-0 mb-2 border"
            onChange={e=>handleTextChange(e, 'name')}/>

          {btnCreatePostoffice()}
        </Col>
      </Row>
    </Container>
  </Container>

  const fundingBox = hashLink('/postOffice/funding',<Container fluid={true} className="m-0 p-3 alert-info rounded hover-shadow">
    <span className="mr-2 float-left">{metaImage.showOrigin('postOffice',
    {
      className: 'border border-secondary shadow-sm',
      style: {width:'3rem',}
                    
    }) }</span>
    <span className="float-left">
      <Container className="mr-3">Invest via Mainne</Container>
      <Container className="mr-3">Create Post Office under Ethereum mainnet. Each post office contain 1,000,000 UNST and 1000 
      placeholder projects. </Container>
    </span>
    <Container className="clearfix"/>
  </Container>)

  return (
    <Container fluid={true} className="p-3" style={{minHeight:'32rem'}}>
      {poAddress === 'funding' ? formForm: fundingBox}
      <Container fluid={true} className="p-0">
        {getPostOffices().map((v,k)=> {
            return hashLink('/postOffice/' + v.address,<Container fluid={true} key={k} className={'m-0 mt-3 p-3 alert-secondary rounded hover-shadow'}>
              <span className="mr-3">{metaImage.showOrigin('postOffice', 
                {
                  className: 'border border-secondary shade',
                  style: {width:'3rem', borderRadius: '50%'}
                })}</span>
              <span className="mr-3">{copyableShow(v.address, 5)} </span>
              <span className="mr-3">{v.name} </span>
            </Container>);
          })
          }
      </Container>
    </Container>
  );
}